$(window).on("load", function () {
    $(".se-pre-con").fadeOut("slow");;
});

$(document).ready(function () {
    var url = window.location.href.replace("?m=1", "");
    var uri = url.substring(url.lastIndexOf("/") + 1);
    switch (uri) {
        case "quem-somos":
            $("#main__about").show();
            $("." + uri).addClass("active");
            break;
        case "servicos":
            $("#main__services").show();
            break;
        case "contato":
            $("#main__contact").show();
            break;
        case "casamento":
            $("#main__casamento").show();
            break;
        default:
            $("#main__home").show();
            break;
    }

    // Menu activation
    switch (uri) {
        case "quem-somos":
        case "servicos":
        case "contato":
            $("." + uri).addClass("active");
            break;
        default:
            $(".home").addClass("active");
            break;
    }

    $('a[href^="#"].scrollable').on("click", function (event) {
        event.preventDefault();

        var href = $.attr(this, "href")
        $("html, body").animate({ scrollTop: $(href).offset().top - 120 }, 1000);
    });

    // Scroll button
    $("#scroll-btn").on("click", function () {
        scrollToTop();
    });

    // Logo Animation
    var textWrapper = document.querySelector(".ml9 .letters");
    textWrapper.innerHTML = textWrapper.textContent.replace(
        /\S/g,
        "<span class='letter'>$&</span>"
    );

    anime
        .timeline({
            loop: false
        })
        .add({
            targets: ".ml9 .letter",
            scale: [0, 1],
            duration: 1500,
            elasticity: 600,
            delay: (_el, i) => 45 * (i + 1),
        })
        .add({
            targets: ".ml9",
            opacity: 1,
            duration: 1000,
            easing: "easeOutExpo",
            delay: 1000,
        });

    // Referrals
    $(".referrals:not(.slick-initialized)").slick({
        autoplay: true,
        autoplaySpeed: 4000,
    });
    $("button.slick-arrow").empty();

    // Casamento
    $("#swiper-casamento")
        .not(".slick-initialized")
        .slick({
            arrows: true,
            autoplay: false,
            prevArrow: $('.pagination .swiper-button-prev'),
            nextArrow: $('.pagination .swiper-button-next'),
        });

    $('.tab_container input[name="tabs"]').on('change', function () {
        $('.tab_container .tab-content').hide();
        var target = $(this).data('target');
        $('.tab_container #' + target).css('display', 'flex');
    });

    // Beliefs (hidden for now)
    $(".beliefs-list")
        .not(".slick-initialized")
        .slick({
            dots: true,
            autoplay: true,
            autoplaySpeed: 8000,
        })
        .on("beforeChange", function (_event, _slick, _currentSlide, nextSlide) {
            $(".nav-link").removeClass("active");
            $(".nav-" + (nextSlide + 1)).addClass("active");
        });
    $(".beliefs button.slick-arrow, .slick-dots").hide();

    $(".nav-beliefs a").each(function (i) {
        $(this).click(function () {
            $(".slick-dots button[id$='" + i + "']").click();
        });
    });

    // Menu
    $("div.burger").on("click", function () {
        if (!$(this).hasClass("open")) {
            openMenu();
        } else {
            closeMenu();
        }
    });

    $("div.menu ul li a").on("click", function () {
        closeMenu();
    });

    // Popups
    $("#our-packages").magnificPopup({
        delegate: "a.see-more",
        removalDelay: 100, //delay removal by X to allow out-animation
        callbacks: {
            beforeOpen: function () {
                this.st.mainClass = this.st.el.attr("data-effect");
            },
        },
        fixedContentPos: true,
        midClick: true, // allow opening popup on middle mouse click. Always set it to true if you don't provide alternative source.
    });

    function openMenu() {
        $("div.circle").addClass("expand");

        $("div.burger").addClass("open");
        $("div.x, div.y, div.z").addClass("collapse");
        $(".menu li").addClass("animate");

        setTimeout(function () {
            $("div.y").hide();
            $("div.x").addClass("rotate30");
            $("div.z").addClass("rotate150");
        }, 70);
        setTimeout(function () {
            $("div.x").addClass("rotate45");
            $("div.z").addClass("rotate135");
        }, 120);
    }

    function closeMenu() {
        $("div.burger").removeClass("open");
        $("div.x").removeClass("rotate45").addClass("rotate30");
        $("div.z").removeClass("rotate135").addClass("rotate150");
        $("div.circle").removeClass("expand");
        $(".menu li").removeClass("animate");

        setTimeout(function () {
            $("div.x").removeClass("rotate30");
            $("div.z").removeClass("rotate150");
        }, 50);
        setTimeout(function () {
            $("div.y").show();
            $("div.x, div.y, div.z").removeClass("collapse");
        }, 70);
    }
});

var sticky = $("header").offset().top;

window.onscroll = function () {
    watchScrollDown();
    scrollFunction();
};

function scrollFunction() {
    if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
        $("#scroll-btn").show();
    } else {
        $("#scroll-btn").hide();
    }
}

function scrollToTop() {
    const c = document.documentElement.scrollTop || document.body.scrollTop;
    if (c > 0) {
        window.requestAnimationFrame(scrollToTop);
        window.scrollTo(0, c - c / 8);
    }
}

function watchScrollDown() {
    if (window.scrollY > sticky) {
        $("header h1, header, header .header").addClass("scrolled");
    } else {
        $("header h1, header, header .header").removeClass("scrolled");
    }
}